export default {
  name: 'CgoUserShopPermmit',
  props: {
    sid: {
      type: String,
      default: ''
    },
    need: {
      type: String,
      default: 'MNG'
    }
  },
  computed: {
    hasPermit () {
      return this.$store.getters['xd/general/xdgcuser/hasShopPermit'](this.$props.sid, this.$props.need)
    }
  }
}
